function bolding(text, bolding) {
  const words = text.split(bolding);
  return (
    <span>{words[0]}<b>{bolding}</b>{words[1]}</span>
  );
}

function createRows(classes, rows) {
  return (
    rows.map((row) => (
      <li className="listItem"><p>{bolding(row.content, "Hiroki Yoshikawa")} doi:<a href={row.url} target="_blank" rel="noopener noreferrer">{row.url}</a></p></li>
    ))
  );
}
function createPatent(classes, rows) {
  return (
    rows.map((row) => (
      <li className="listItem"><p>{bolding(row.content, "吉川 寛樹")}</p></li>
    ))
  );
}
function createAward(classes, rows) {
  return (
    rows.map((row) => (
      <li className="listItem"><p>{bolding(row.content, row.award)}</p></li>
    ))
  );
}

